
import {Options, setup, Vue} from 'vue-class-component'
import axios from 'axios'
import util from '../util'
import SequencePlayer from '../SequencePlayer'
import BlushButton from "@/components/BlushButton.vue";
import BlushThumb from "@/components/BlushThumb.vue";

@Options({
  components: {
    BlushButton, BlushThumb
  },
  props: {
  },
  methods: {
    async getThumb(id: string) {
      let info = await util.getInfo(id)
      return `${util.baseUrl()}/${id}${info.thumb}`
    }
  },
  async beforeCreate() {
    let response = await axios.get(util.baseUrl() + '/gallery-info.json')
    this.sequences = response.data.sequences
    for (let id of this.sequences) {
      let info = await util.getInfo(id)
      this.allSequences.push(new SequencePlayer(id, info))
    }
  }
})
export default class Gallery extends Vue {
  sequences: string[] = []
  allSequences: SequencePlayer[] = []
}
