<template>
  <div :id="this.id" class="image-view">
    <div class="image-container">
      <video v-if="this.isVideo(img)" class="current-image" controls :poster="this.sequencePlayer.getThumb()">
        <source :src="img" type="video/webm">
      </video>
      <img v-else class="current-image" v-bind:src="img">
    </div>
    <div class="control-panel">
      <BlushButton v-if="!this.isVideo(img)" v-on:click="this.firstB()">
        <img src="@/assets/toStart.svg" alt="" class="svg-2">
      </BlushButton>
      <BlushButton v-if="!this.isVideo(img)" v-on:click="this.prevB()">
        <img src="@/assets/back.svg" alt="" class="svg-2">
      </BlushButton>
      <BlushButton v-if="!this.isVideo(img)" v-on:click="this.nextB()">
        <img src="@/assets/next.svg" alt="" class="svg-2">
      </BlushButton>
      <BlushButton v-if="!this.isVideo(img)" v-on:click="this.playB()">
        <span v-if="paused"><img src="@/assets/play.svg" alt="" class="svg-2"></span>
        <span v-else><img src="@/assets/pause.svg" alt="" class="svg-2"></span>
      </BlushButton>
      <BlushButton v-on:click="this.exitB()">
        <img src="@/assets/exit.svg" alt="" class="svg-2">
      </BlushButton>
    </div>
    <div class="info-panel">
      <div id="titlebar">
        <h1 id="title">{{ info.title }}</h1>
        <h3 id="index-counter">{{ sequencePlayer.getCurrentIndex() }} / {{ sequencePlayer.getLastIndex() }}</h3>
      </div>
      <div id="description" v-html="description"></div>
    </div>
    <div v-for="item in extraImages">
      <img class="thumb-container" v-bind:src="this.sequencePlayer.getThumb()" v-on:click="this.showImage(this.sequencePlayer.get())">
      <img v-if="this.isVideo(item)" class="thumb-container" src="@/assets/video.svg" v-on:click="this.showImage(item)">
      <img v-else class="thumb-container" v-bind:src="item" v-on:click="this.showImage(item)">
    </div>
    <PatreonButton />
  </div>
</template>

<script lang="ts">
import {Options, setup, Vue} from 'vue-class-component'
import axios from 'axios'
import util from '../util'
import SequencePlayer from '../SequencePlayer'
import BlushButton from "@/components/BlushButton.vue";
import Hjson from 'hjson'
import PatreonButton from "@/components/PatreonButton.vue";

@Options({
  components: {
    BlushButton, PatreonButton
  },
  props: {
  },
  methods: {
    log (msg: any) {
      console.log(msg)
    },
    async play() {
      await util.wait(200)
      if (this.sequencePlayer.getCurrentIndex() == this.sequencePlayer.getLastIndex()) {
        await util.wait(1000) // Stay extra on the last image
        if (this.paused) return
        this.firstB()
        await util.wait(800)
        if (this.paused) return
      }
      this.nextB()
      await util.wait(800)
      if (!this.paused) this.play()
    },
    playB() {
      this.paused = !this.paused
      if (!this.paused) this.play()
    },
    firstB() {
      this.img = this.sequencePlayer.goto(0).get()
    },
    prevB() {
      this.img = this.sequencePlayer.prev().get()
    },
    nextB() {
      this.img = this.sequencePlayer.next().get()
    },
    exitB() {
      this.$router.push('/')
    },
    showImage(url:string) {
      this.img = url
    },
    isVideo(url: string) {
      return /\.webm$/i.test(url)
    }
  },
  async beforeCreate() {
    this.id = this.$route.params.id
    this.info = await util.getInfo(this.id)
    this.description = this.info.description
    this.sequencePlayer = new SequencePlayer(this.id, this.info)
    this.img = this.sequencePlayer.get()

    if (this.info.extra && this.info.extra.length > 0) {
      for (let path of this.info.extra) {
        this.extraImages.push(util.baseUrl() + '/' + this.id + path)
      }
    }
    //this.allImages = this.info.extra
  }
})
export default class ImageView extends Vue {
  id!: string
  img = ''
  description = ''
  info: any = {
    title: "",
    firstImage: ""
  }
  sequencePlayer: SequencePlayer = new SequencePlayer("none", this.info)
  extraImages: string[] = []
  paused: boolean = true
}
</script>

